.main {
  background-color: black;
  color: white;
  height: 640px;
  width: 100%;
  margin-top: 60px;
  padding-top: 10px;
}
.aboutDiv {
  margin: auto;
  text-align: center;
  margin-top: 20px;
  padding-bottom: 10px;
}

.description {
  box-shadow: rgba(30, 144, 255) 0px 5px 10px;
  height: 480px;
  width: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  gap: 10px;
  text-align: center;
  padding: 30px;
  margin:auto
}

.rani {
  box-shadow: 0 0 0 3px rgba(30, 144, 255);
  height: 400px;
  border-radius: 50%;
  margin: 10px;
}

.resume {
  background-color: rgba(30, 144, 255);
  color: black;
  font-size: 20px;
  font-weight: bold;
  height: 40px;
  width: 100px;
  margin: 30px;
}

.resume:hover {
  cursor: pointer;
}
.lines {
  height: 500px;
  margin-top: 20px;
}

.lines > p {
  font-size: 25px;
}

.hi{
  padding:20px;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .main {
    height: 1000px;
    width: 100%;
  }
  .description {
    height: 800px;
    width: 82%;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (min-width: 1024px) and (max-width: 1100px) {
  .main {
    height: 1000px;
    width: 100%;
  }
  .description {
    height: 800px;
    width: 85.5%;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .main {
    height: 900px;
  }

  .rani {
    height: 30%;
  }
  .description {
    width: 65%;
    height: 75%;
    grid-template-columns: repeat(1, 1fr);
  }
  .lines {
    height: 90%;
    margin-top: -210%;
  }
  .lines > p {
    font-size: 18px;
  }
}
